#revenue{
    background-color: #FFFFFF;
    border-radius: 16px;
    padding-left: 24px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 10px;
}
header{
    font-size: 20px;
    font-weight: 600;
    padding: 0;
    margin: 0;
}
#percent-holder{
    display: flex;
    align-items: center;
    gap: 10px;
}
#percent{
    font-size: 30px;
    font-weight: 500;
    text-align: left;
}
#text{
    font-size: 14px;
    font-weight: 400;
    text-align: left;
    color: #454545
}
#extra-info{
    font-size: 14px;
    font-weight: 400;
    text-align: left;
    color: #734A00;
}